<template>
    <div class="qr-code">
        <div v-if="$route.name == '参数二维码'">
            <Button type="primary" style="margin-bottom: 30px;" to="/manage/weixinTool/qrCode/create">新增参数二维码</Button>
            <Table class="qr-code-table" stripe :columns="qrHeader" :data="qrData"></Table>
        </div>
        <router-view />
    </div>
</template>

<script>
export default {
  data() {
    return {
      qrData: [],
      qrHeader: [
        {
          key: "name",
          title: "二维码名称",
          align: "center"
        },
        {
          key: "content",
          title: "关注后回复内容",
          align: "center"
        },
        {
          key: "gonghao",
          title: "公号",
          align: "center"
        },
        {
          key: "qr_code_url",
          title: "二维码",
          align: "center",
          render: (h, params) => {
            return h("img", {
              attrs: {
                src: params.row.qr_code_url,
                style: "width:60%;padding:10px;"
              }
            });
          }
        },
        {
          title: "Action",
          align: "center",
          width: 200,
          render: (h, params) => {
            let that = this;
            return h("div", [
              h(
                "Button",
                {
                  style: {
                    marginRight: "10px"
                  },
                  props: {
                    type: "primary",
                    size: "small"
                  },
                  on: {
                    click() {
                      that.$router.push({
                        name: "编辑二维码",
                        params: params.row
                      });
                    }
                  }
                },
                "修改"
              ),
              h(
                "Button",
                {
                  props: {
                    type: "error",
                    size: "small"
                  },
                  on: {
                    click() {
                      that.$Modal.confirm({
                        title: "提示:",
                        content: "确认删除这条内容吗？",
                        onOk: () => {
                          that.$axios
                            .post("/qr_code/deleteOne", { id: params.row._id })
                            .then(res => {
                              that.$Message.info(res.data.success);
                              if (res.data.data == "") {
                                that.qrData = [];
                              } else {
                                that.showList();
                              }
                            });
                        }
                      });
                    }
                  }
                },
                "删除"
              )
            ]);
          }
        }
      ]
    };
  },
  mounted() {
    this.showList();
  },
  methods: {
    showList() {
      this.$axios.get("/qr_code/show").then(res => {
        var codes = res.data.codes;
        var data = res.data.data;
        for (var i = 0, codesLength = codes.length; i < codesLength; i++) {
          for (var j = 0, dataLength = data.length; j < dataLength; j++) {
            if (codes[i].code == data[j].code) {
              data[j].gonghao = codes[i].name;
            }
          }
        }
        this.qrData = data;
      });
    }
  },
  watch: {
    $route(to, from) {
      if (from.name == "创建二维码" || from.name == "编辑二维码") {
        this.showList();
      }
    }
  }
};
</script>

<style scoped>
.wrapper {
  margin-top: 30px;
  border: 1px solid #eee;
}

.wrap-title {
  padding: 20px;
  text-align: center;
}

.qr_img {
  width: 100px;
  height: 100px;
}

.tag-name {
  width: 150px;
  height: 30px;
  line-height: 30px;
  border: 1px solid #eee;
  border-radius: 5px;
  padding-left: 10px;
}
.tag-name input {
  padding-left: 0;
  width: 80px;
  border: 0;
  outline: none;
}
.tag-list li {
  float: left;
  margin-right: 10px;
  cursor: pointer;
}
</style>